/************************************************

Stylesheet: Global Stylesheet

*************************************************/

/*********************
Layout
*********************/

body {
	overflow-x: hidden;
	cursor: url(../images/cursoret.png), auto !important;
}

a {
	cursor: url(../images/cursoret.png), auto !important;
}

select {
	cursor: url(../images/cursoret.png), auto !important;
}

.page-content {

	@include breakpoint (medium) {
		margin-top: 100px;
	}

}

.single-article {
	@include breakpoint (medium) {
		margin-top: 100px;
	}

}

.content {
	@include breakpoint (small only) {
		padding-top: 100px;
		// margin-bottom: 40px;
		// padding-bottom: 40px;
		width: 90%;
		margin-left: 5%;
		margin-right: 5%;
	}
	
	width: 80%;
	margin-left: 10%;
	margin-right: 10%;
	clear: both;
}
.type-dm_event{
	@include breakpoint (small only) {
		padding-top: 0px;
	}
}

.nf-form-content .privacy-checkbox label {
	font-size: 16px;
	font-weight: 400;
	@include breakpoint (small only) {
		margin-top: 16px!important;
	}
}
.small-content {
	padding-top: 100px;
	clear: both;
	width: 80%;
	margin-left: 10%;
	margin-right: 10%;


	@include breakpoint (medium) {
		width: 50%;
		margin: 0 auto;
	}
}

.submit-button {
	background-color: $secondary-color;

	&:hover {
		background-color: scale-color($secondary-color, $lightness: -15%);
	}
}

.button {
	cursor: url(../images/cursoret.png), auto !important;
}

.hollow:hover {
	background-color: #fefefe !important;
	color: #000 !important;

	a {
		color: #000 !important;
	}
}

.nf-form-content .ninja-forms-field.button{
	background: $secondary-color;
	color: $white;
	&:hover {
		background-color: scale-color($secondary-color, $lightness: -15%);
		color: $white!important;
	}
}

.button-center {
	text-align: center;
	@include breakpoint (medium) {
		text-align: left;

	}
}

.alignright{
	text-align: right;
}
::placeholder {
	color: #333333;
}

/*********************
POSTS & CONTENT STYLES
*********************/
.page-title {

	// Remove Foundation vcard styling from author page titles
	.vcard {
		border: 0px;
		padding: 0px;
	}
}

// Post meta  
.byline {
	color: #999;

	time {}
}

// Entry content  
.article-header {
	text-align: center;
}

.orbit-figure {
	img {
		width: 100%;
	}
}

.single-title {
	
	margin-top: 1rem;
	font-size: 2rem;
}

.text-uppercase {
	text-transform: uppercase;
}

.negative-margin {
	margin: 0 -20px;
	@include breakpoint (medium) {
		margin: unset;
	}


}

.alignfull {
	margin: 0 calc(50% - 50vw);
	max-width: 100vw;
	width: 100vw;
}

.entry-content {

	img {
		max-width: 100%;
		height: auto;
	}

	.alignleft,
	img.alignleft {
		margin-right: 1.5em;
		display: inline;
		float: left;
	}

	.alignright,
	img.alignright {
		margin-left: 1.5em;
		display: inline;
		float: right;
	}

	.aligncenter,
	img.aligncenter {
		text-align: center;
		margin-right: auto;
		margin-left: auto;
		display: block;
		clear: both;
	}

	video,
	object {
		max-width: 100%;
		height: auto;
	}

	pre {
		background: #eee;
		border: 1px solid #cecece;
		padding: 10px;
	}

}

.wp-caption {
	max-width: 100%;
	background: #eee;
	padding: 5px;

	// Images inside wp-caption  
	img {
		max-width: 100%;
		margin-bottom: 0;
		width: 100%;
	}

	p.wp-caption-text {
		font-size: 0.85em;
		margin: 4px 0 7px;
		text-align: center;
	}
}

// Add Foundation styling to password protect button
.post-password-form input[type="submit"] {
	@include button;
}

/*********************
IMAGE GALLERY STYLES
*********************/
.gallery {
	max-width: 100%;
	margin-left: auto;
	margin-right: auto;
	margin-right: -15px;
	margin-left: -15px;
}

.gallery::before,
.gallery::after {
	content: ' ';
	display: table;
}

.gallery::after {
	clear: both;
}

.gallery.gallery-columns-1>dl {
	@include grid-column;
}

.gallery.gallery-columns-2>dl {
	@include grid-column(1 of 2);
}

.gallery.gallery-columns-3>dl {
	@include grid-column(1 of 3);
}

.gallery.gallery-columns-4>dl {
	@include grid-column(1 of 4);
}

.gallery.gallery-columns-5>dl {
	@include grid-column(1 of 5);
}

.gallery.gallery-columns-6>dl {
	@include grid-column(1 of 6);
}

.gallery.gallery-columns-7>dl {
	@include grid-column(1 of 7);
}

.gallery.gallery-columns-8>dl {
	@include grid-column(1 of 8);
}

.gallery dl {
	width: 100%;
	float: left;
	padding-left: 0.9375rem;
	padding-right: 0.9375rem;
}

.gallery dl:last-child:not(:first-child) {
	float: right;
}

// Gallery caption styles
.gallery-caption {}

.size-full {}

.size-large {}

.size-medium {}

.size-thumbnail {}

/*********************
PAGE NAVI STYLES
*********************/
.page-navigation {
	margin-top: 1rem;
}

